@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@600&display=swap);
*, *::after {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  width: 100vw;
  background-color: #FF9D0A;
  color: #3C096C;
  font-family: 'Poppins', 'Oxygen', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.login {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

input, button {
  border: none;
  background: none;
  color: #3C096C;
}

input:focus{
  outline: none;
}

#password {
  padding: 1rem;
  border-radius: 25px;
  border-top-left-radius: 18px;
  border-bottom-right-radius: 18px;
  border-left: 2px #3C096C solid;
  border-bottom: 2px #3C096C solid;
  font-size: 1.5rem;
}

button {
  height: 8rem;
  border-radius: 50%;
  margin-left: -2rem;
  margin-top: -1.5rem;
  cursor: pointer;
}

.login img {
  height: 8rem;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 1px 1px 4px grey;
}
.letter {
    position: relative;
    z-index: 0;
    max-height: 100vh;
    display: none;
    overflow-y: hidden;
    -ms-scroll-snap-type: x mandatory;
        scroll-snap-type: x mandatory;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.letter::-webkit-scrollbar {
    display: none;
  }

.letter-section {
    display: flex;
    align-items: center;
    height: 100vh;
    min-width: 100vw;
    overflow: hidden;
    scroll-snap-align: start;
}

.s2 {
    justify-content: center;
}

.s3 {
    justify-content: flex-end;
}

.bubble-text {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 6rem;
    width: 50vw;
    aspect-ratio: 1/1;
    border-radius: 50%;
}

.bubble-text::after {
    content: "";
    position: absolute;
    z-index: -1;
    inset: 0;
    border-radius: 50%;
    background-color: #3C096C;
    -webkit-animation: float 4s ease 1s infinite;
            animation: float 4s ease 1s infinite;
}

.p {
    color: #FFDEAD;
    text-align: justify;
}

.scroll {
    align-self: flex-end;
    margin: 0 0 5rem 2rem;
    font-family: "Gluten";
    font-size: 2rem;
}

@-webkit-keyframes float {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(3%);
                transform: translateY(3%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

}

@keyframes float {
    0% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }
    50% {
        -webkit-transform: translateY(3%);
                transform: translateY(3%);
    }
    100% {
        -webkit-transform: translateY(0%);
                transform: translateY(0%);
    }

}
